"use client";

import React, { useRef, useMemo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { Float, Cylinder } from '@react-three/drei';
import * as THREE from 'three';

function Capsule({ position, color1, color2, rotation }: any) {
    return (
        <group position={position} rotation={rotation}>
            <mesh position={[0, 0.25, 0]}>
                <cylinderGeometry args={[0.2, 0.2, 0.5, 32]} />
                <meshPhongMaterial color={color1} emissive={color1} emissiveIntensity={0.5} />
            </mesh>
            <mesh position={[0, -0.25, 0]}>
                <cylinderGeometry args={[0.2, 0.2, 0.5, 32]} />
                <meshPhongMaterial color={color2} emissive={color2} emissiveIntensity={0.5} />
            </mesh>
            <mesh position={[0, 0.5, 0]}>
                <sphereGeometry args={[0.2, 32, 32, 0, Math.PI * 2, 0, Math.PI / 2]} />
                <meshPhongMaterial color={color1} emissive={color1} emissiveIntensity={0.5} />
            </mesh>
            <mesh position={[0, -0.5, 0]} rotation={[Math.PI, 0, 0]}>
                <sphereGeometry args={[0.2, 32, 32, 0, Math.PI * 2, 0, Math.PI / 2]} />
                <meshPhongMaterial color={color2} emissive={color2} emissiveIntensity={0.5} />
            </mesh>
        </group>
    );
}

function Capsules() {
    const capsules = useMemo(() => {
        return Array.from({ length: 30 }).map((_, i) => ({
            position: [
                (Math.random() - 0.5) * 25,
                (Math.random() - 0.5) * 25,
                (Math.random() - 0.5) * 25
            ] as [number, number, number],
            rotation: [Math.random() * Math.PI, Math.random() * Math.PI, Math.random() * Math.PI] as [number, number, number],
            color1: i % 2 === 0 ? "#f87171" : "#fbbf24",
            color2: "#ffffff",
            speed: 0.5 + Math.random() * 1.5,
        }));
    }, []);

    return (
        <group>
            {capsules.map((c, i) => (
                <Float key={i} speed={c.speed} rotationIntensity={2} floatIntensity={1}>
                    <Capsule {...c} />
                </Float>
            ))}
        </group>
    );
}

export default function MedicineCapsulesBackground() {
    return (
        <Canvas camera={{ position: [0, 0, 15], fov: 45 }}>
            <ambientLight intensity={0.5} />
            <pointLight position={[10, 10, 10]} intensity={1} />
            <Capsules />
            <fog attach="fog" args={['#000', 10, 30]} />
        </Canvas>
    );
}
