"use client";

import React, { useRef, useMemo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { Float, Sphere, MeshDistortMaterial } from '@react-three/drei';
import * as THREE from 'three';

function GeneStructure() {
    const group = useRef<THREE.Group>(null);

    const particles = useMemo(() => {
        const temp = [];
        for (let i = 0; i < 50; i++) {
            const radius = 2 + Math.random() * 8;
            const speed = 0.5 + Math.random();
            const angle = Math.random() * Math.PI * 2;
            temp.push({ radius, speed, angle, size: 0.1 + Math.random() * 0.2 });
        }
        return temp;
    }, []);

    useFrame((state) => {
        if (!group.current) return;
        group.current.rotation.y += 0.005;
        group.current.rotation.z += 0.002;
    });

    return (
        <group ref={group}>
            {/* Central "DNA" sequence simulation */}
            {Array.from({ length: 20 }).map((_, i) => (
                <React.Fragment key={i}>
                    <Float speed={2} rotationIntensity={2} floatIntensity={1}>
                        <Sphere args={[0.15, 16, 16]} position={[Math.sin(i * 0.5), i * 0.5 - 5, Math.cos(i * 0.5)]}>
                            <meshPhongMaterial color="#4ade80" emissive="#22c55e" emissiveIntensity={2} transparent opacity={0.8} />
                        </Sphere>
                    </Float>
                    <Float speed={2} rotationIntensity={2} floatIntensity={1}>
                        <Sphere args={[0.15, 16, 16]} position={[Math.sin(i * 0.5 + Math.PI), i * 0.5 - 5, Math.cos(i * 0.5 + Math.PI)]}>
                            <meshPhongMaterial color="#3b82f6" emissive="#1d4ed8" emissiveIntensity={2} transparent opacity={0.8} />
                        </Sphere>
                    </Float>
                </React.Fragment>
            ))}

            {/* Floating molecules */}
            {particles.map((p, i) => (
                <Float key={i} speed={p.speed} rotationIntensity={1} floatIntensity={2}>
                    <mesh position={[Math.sin(p.angle) * p.radius, (i % 10) - 5, Math.cos(p.angle) * p.radius]}>
                        <sphereGeometry args={[p.size, 16, 16]} />
                        <MeshDistortMaterial
                            speed={2}
                            distort={0.4}
                            color={i % 2 === 0 ? "#4ade80" : "#3b82f6"}
                            emissive={i % 2 === 0 ? "#22c55e" : "#1d4ed8"}
                            emissiveIntensity={1}
                        />
                    </mesh>
                </Float>
            ))}
        </group>
    );
}

export default function GenesBackground() {
    return (
        <Canvas camera={{ position: [0, 0, 15], fov: 45 }}>
            <ambientLight intensity={0.5} />
            <pointLight position={[10, 10, 10]} intensity={1} color="#4ade80" />
            <pointLight position={[-10, -10, -10]} intensity={1} color="#3b82f6" />
            <GeneStructure />
            <fog attach="fog" args={['#000', 10, 25]} />
        </Canvas>
    );
}
