"use client";

import React, { useRef, useMemo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import * as THREE from 'three';

function Beams() {
    const count = 40;
    const meshRef = useRef<THREE.InstancedMesh>(null);

    const beams = useMemo(() => {
        const data = [];
        for (let i = 0; i < count; i++) {
            data.push({
                position: new THREE.Vector3(
                    (Math.random() - 0.5) * 30,
                    (Math.random() - 0.5) * 30,
                    (Math.random() - 0.5) * 30
                ),
                speed: 0.1 + Math.random() * 0.4,
                scale: 0.1 + Math.random() * 0.5,
            });
        }
        return data;
    }, []);

    const dummy = new THREE.Object3D();

    useFrame((state) => {
        if (!meshRef.current) return;
        beams.forEach((beam, i) => {
            beam.position.x += beam.speed;
            if (beam.position.x > 15) beam.position.x = -15;

            dummy.position.copy(beam.position);
            dummy.scale.set(beam.scale * 10, 0.02, 0.02);
            dummy.updateMatrix();
            meshRef.current?.setMatrixAt(i, dummy.matrix);
        });
        meshRef.current.instanceMatrix.needsUpdate = true;
    });

    return (
        <instancedMesh ref={meshRef} args={[new THREE.BoxGeometry(), new THREE.MeshBasicMaterial(), count]}>
            <meshBasicMaterial color="#ef4444" transparent opacity={0.6} />
        </instancedMesh>
    );
}

export default function LaserBeamsBackground() {
    return (
        <Canvas camera={{ position: [0, 0, 15], fov: 45 }}>
            <Beams />
            <fog attach="fog" args={['#000', 10, 25]} />
        </Canvas>
    );
}
